@import '../Variables.scss';

footer
{
    background-color: $primary-main-color;
    color: $secondary-sub-color;
    box-shadow: 0rem -0.25rem 2rem 0.25rem $primary-main-color;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: $foreground-far-z-index;
}

.copyrightText
{
    font-weight: bold;
    font-size: small;
    text-align: center;
    margin: 0;
    padding: 0.5rem;
    border-top: 1px solid $secondary-sub-color;
}