@import '../Variables.scss';

html, body, #root
{
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

body
{
  background-color: $secondary-sub-color;
}

input.form-control
{
  background-color: $secondary-sub-color;
  border-color: darken($secondary-sub-color, 10%);
  color: $primary-sub-color;
  border-radius: 0.125rem;

  &:focus
  {
    background-color: $secondary-sub-color;
    border-color: $secondary-main-color;
    color: $primary-sub-color;
    box-shadow: none;
  }

  &::placeholder
  {
    color: $primary-sub-color;
    opacity: 0.5;
  }
}

span.input-group-text
{
  background-color: $primary-sub-color;
  border-color: darken($primary-sub-color, 10%);
  color: $secondary-sub-color;
  border-radius: 0.125rem;
}

h1, h2, h3, h4, h5, h6
{
  font-family: $primary-font-family;
}

p
{
  font-family: $secondary-font-family;
}