$primary-main-color: saddlebrown;
$primary-sub-color: lighten($primary-main-color, 25%);
$secondary-main-color: goldenrod;
$secondary-sub-color: lighten($secondary-main-color, 37.5%);
$primary-font-family: 'Grenze Gotisch', cursive;
$secondary-font-family: 'Grenze', serif;
$small-size: 1rem;
$medium-size: 2rem;
$large-size: 3rem;
$screen-xs-max-width: 575px;
$screen-sm-min-width: 576px;
$screen-md-min-width: 768px;
$screen-lg-min-width: 992px;
$screen-xl-min-width: 1200px;
$background-far-z-index: -2;
$background-near-z-index: -1;
$middleground-z-index: 0;
$foreground-near-z-index: 1;
$foreground-far-z-index: 2;